/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/

// Sidebar styles


.widget-area {
	width: 27%;
	padding-left: 3.5rem;
	padding-top: 3rem;
	padding-bottom: 3rem;
	border-left: 1px solid #e1e1e1;
	.widget {
		margin: 0 0 3rem;
		line-height: 1.75rem;

		h2 {
			color: $color__purple-brand;
			margin-bottom: $base__space-unit;
		}

		& a {
			text-decoration: none;
			color: $color__green-brand;
			&:hover {
				color: $color__purple-brand;
			}
		}

		& ul {
			list-style-type: none;
			margin-left: 0;
			padding-left: 0;
		}

		.widget-contact-text:nth-child(3) {
			display: flex;
			flex-flow: row nowrap;
			align-items: flex-start;

			& i {
				height: 100%;
			}

		}

		.widget-contact-text, .widget-hours-text {
			margin-bottom: 0.5rem;

			.fa-phone:before {
				content: "\f095";
			}

			.fa-envelope:before {
				content: "\f0e0";
			}

			.fa-map-marker:before {
				content: "\f041";
			} 

			.day-of-week {
				font-weight: bold;
			}

			& i {
				margin-top: 0.5rem;
				margin-right: 0.5rem;
			}
		}
}

	/* Make sure select elements fit in widgets. */
	select {
		max-width: 100%;
	}
}

/* Search widget. */
.widget_search .search-submit {
	display: none;
}
